@import './variables.scss';

.section-content-container {
 position: relative;

 .section-content.bg.yellow {
 background-color: $sc-primary-color;
 }

 .section-content.bg {
 padding: 25px;
 background-repeat: no-repeat;
 background-position: center;
 background-size: cover;
 background-blend-mode: multiply;
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;

 /* Add the blur effect */
 filter: blur(1px);
 -webkit-filter: blur(1px);
 }

 .title {
 position: absolute;
 color: white;
 }
}

.card-group {
 margin-bottom: 20px;
}

.row.align-items-center {
 text-align: center;
}

.title {
 position: relative;
 text-align: center;
 font-family: 'Montserrat', sans-serif;
 letter-spacing: 5px;
 width: 100%;

 .title-img {
 max-width:60%;
 max-height: 60%;
 }
}

.spacing {
 padding-top: 5%;
 visibility: hidden;
}

.overlay {
 position: absolute;
 top: 0;
 bottom: 0;
 left: 0;
 right: 0;
 height: 100%;
 width: 100%;
 opacity: 0;
 transition: .5s ease;
 background-color: rgba(232, 200, 17, 1);
}

.overlay-text {
 color: white;
 font-size: 20px;
 position: absolute;
 top: 50%;
 left: 50%;
 -webkit-transform: translate(-50%, -50%);
 -ms-transform: translate(-50%, -50%);
 transform: translate(-50%, -50%);
 text-align: center;
}

.card:hover .overlay {
 opacity: 1;
}

.card-body {
 font-family: 'Montserrat', sans-serif;
 font-size: 14px;
 height: auto;
}

.card-title {
 font-size: 25px;
}

.card-img-top {
 img {
 min-height: 300px;
 max-height: 300px;
 }
}
